.ripple {
  position: relative;
  overflow: hidden;
  -webkit-transform: translate(0);
}
.effekt {
  display: block;
  position: absolute;
  pointer-events: none;
  border-radius: 50%;
  transform: scale(0);
  z-index:2;
  background: rgba(52, 152, 219, 1);
  opacity: 1;
  transition: transform 1s cubic-bezier(0.165, 0.85, 0.45, 1);
}
.effekt.replay {

	opacity: 1;
	-webkit-transform: scale(2);
	-ms-transform: scale(2);
	-o-transform: scale(2);
	transform: scale(2);

  will-change: transform;
}



