.h_border {
  color: white;
  font-size: 16px;
  font-weight: 500;
  line-height: 50px;
  text-transform: uppercase;
  position: relative;
  cursor: pointer;
  text-decoration: none;
  display: inline-block;
  padding: 0px 30px;
}

.h_border::after {
  content: '';
  display: inline-block;
  position: absolute;
  left: 0px;
  border-top: 2px solid;
  border-bottom: 2px solid;
  height: 100%;
  bottom: 0px;
  right: 0px;
  margin: auto;
  width: calc(100% - 60px);
  transition: width .1s linear .6s;
}

.h_border:hover::after {
  width: 100%;
  transition: width .1s linear;
}

.h_border::before {
  content: '';
  display: inline-block;
  position: absolute;
  left: 0px;
  border-left: 2px solid;
  border-right: 2px solid;
  width: calc(100% - 4px);
  height: 0%;
  bottom: 0px;
  transition: height .3s linear .3s;
}

.h_border:hover::before {
  transition: height .3s linear .1s;
  height: calc(100% + 4px);
}

.h_border i {
  position: absolute;
  top: -4px;
  width: 100%;
  height: 2px;
  z-index: 10;
  right: 0px;
  margin: auto;
  left: 0px;
  transition: width .3s linear;
}

.h_border.white_back i {
  background: #fff;
}

.h_border.gold_back i {
  background: #c99d66;
}

.h_border.green_back i {
  background: #00a555;
}

.h_border.white_line::after,
.h_border.white_line::before {
  border-color: #fff !important;
}

.h_border.golden_line::after,
.h_border.golden_line::before {
  border-color: #c99d66 !important;
}

.h_border.green_line::after,
.h_border.green_line::before {
  border-color: #1ab26b !important;
}

.h_border:hover i {
  transition: width .3s linear .4s;
  width: 0%;
}

