// color
.c_sv_bl(){
	color: #292929;
}

.c_sv_bl_h(){
	color: #292929;
	display: inline-block;
	position: relative;

	&::after{
  	content: "";
  	width: 0%;
  	position: absolute;
  	left: 0px;
  	bottom: 0px;
  	height: 1px;
  	background: #292929;
  	transition: width .2s linear;
  }

	&:hover{
		&::after{
			content: "";
			width: 100%;
		}
	}
}

